* {
  box-sizing: border-box;
  margin: 0;
  textarea:focus,
  input:focus {
    outline: none;
  }
  transition: all ease 0.5s;
  font-family: "Roboto";
  scrollbar-width: none;
  &::-webkit-scrollbar {
    display: none;
  }
}
p {
  word-break: keep-all !important;
}

.react-spinner {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #0b001a;

  span {
    box-shadow: 0px 0px 1vmax 0vmax rgba(152, 113, 156, 0.4),
      0px 0px 1vmax 0vmax rgba(131, 0, 255, 0.4);

    background: radial-gradient(circle at center, #df14fa 30%, #743fca 70%);
    background: -webkit-radial-gradient(
      circle at center,
      #b400cc 50%,
      #8300ff 50%
    );
  }
}

.App {
  max-height: 100vh;
  overflow-y: scroll;
  scroll-behavior: smooth;
  background-color: #36244e;
  position: relative;
}
